import React, { forwardRef, useEffect, useImperativeHandle, useState } from "react";
import ReactDOM from "react-dom";

const domNode = typeof(document) !== "undefiend" ? document.querySelector("body") : {};

const ModalContent = (props) => {
    const { showClose, children, modalRef } = props;
    const [ isOpen, setIsOpen ] = useState(false);

    const showModal = () => {
        setIsOpen(true);
    };

    const hideModal = () => {
        setIsOpen(false);
    };

    useEffect(() => {
        document.addEventListener("keyup", (event) => {
            if (event.key === "Escape") {
                hideModal();
            }
        });
    }, []);

    useImperativeHandle(modalRef, () => {
        return {
            showModal,
            hideModal
        }
    });
    

    if (isOpen) {
        return (<div className="modal">
            {showClose && <div className="modal-close-cont">
                <span className="modal-close" onClick={hideModal}>x</span>
            </div>}
            <div className="modal-content">
                <div className="modal-body">
                    {children}
                </div>
            </div>
        </div>);
    } else {
        return null;
    }
};

ModalContent.defaultProps = {
    showClose: true
};

let Modal = (props, ref) => {
    const { children, ...restProps } = props;

    return ReactDOM.createPortal(
        <ModalContent {...restProps} modalRef={ref}>
            {children}
        </ModalContent>,
        domNode
    )
};

Modal = forwardRef(Modal);

export default Modal;