import React, { Fragment, useRef } from "react";
import { EXPERIENCE_ITEMS } from "../data/projects";
import Modal from "./Modal";

const ExperienceDescription = (props) => {
    const { projects } = props;

    return (<div className="projects-description">
        {projects.map(project => {
            const { title, description } = project;

            return (<Fragment key={title}>
                <div>{title}</div>
                <div>{description}</div>
            </Fragment>)
        })}
    </div>)
};

const ExperienceItem = (props) => {
    const { role, company, className, projects } = props;
    const modalRef = useRef();

    const showModal = () => {
        modalRef.current.showModal();
    };

    return (<Fragment>
        <div className={`project-item ${className}`} onClick={showModal}>
            <div className="project-info">
                {/* <div className="name">{title}</div> */}
                <div className="role">{role}</div>
                <div className="company">{company}</div>
            </div>
        </div>
        <Modal ref={modalRef}>
            {<ExperienceDescription projects={projects} />}
        </Modal>
    </Fragment>);
}

const Projects = () => {
    return (<div className="projects-section">
        {EXPERIENCE_ITEMS.map((obj, index) => <ExperienceItem {...obj} key={index} />)}
    </div>);
};

export default Projects;